/*
 * Some best-practice CSS that's useful for most apps
 * Just remove them if they're not what you want
*/

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
}

*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

/* App custum styles */
video {
  margin-top : 10px;
  width: 200px;
}

.camera {
  width: 200px ;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

button {
  margin: 5px;
}

.txt-green {
  color: green;
}

.txt-red {
  color: red;
}



